.page-footer {
  padding-top: 51px;
  padding-bottom: 50px;
  //display: flex;
  //justify-content: flex-start;
  align-items: center;
  //flex-wrap: wrap;
  display: grid;
  grid-template-columns: 70px auto 126px;
  column-gap: 25px;
  background-color: $color-default-tangaroa;
  color: $color-default-alice-blue;

  @include vp-1179 {
    grid-template-columns: 38px auto 106px;
    column-gap: 15px;
    padding-top: 44px;
    padding-bottom: 45px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr auto auto;
    //row-gap: 21px;
    column-gap: 0;
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.page-footer__logo {
  display: flex;
  width: 70px;
  height: 38px;
  //margin-right: 25px;

  @include vp-1179 {
    width: 38px;
    height: 20px;
  }

  @include vp-767 {
    width: 42px;
    height: 22px;
    margin-bottom: 21px;
  }
}

.page-footer__logo svg {
  fill: $color-default-alice-blue;

  @include vp-1179 {
    width: 42px;
    height: 22px;
  }
}

.page-footer__nav {
  //display: flex;
  //justify-content: flex-start;
  //align-items: center;
  @include vp-767 {
    margin-bottom: 17px;
  }
}

.page-footer__nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;

  @include vp-767 {
    flex-direction: column;
    align-items: flex-start;
  }
}

.page-footer__nav li {
  @include vp-767 {
    margin-bottom: 8px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.page-footer__nav a {
  padding: 5px 20px 5px 14px;
  line-height: 20px;
  font-weight: 500;

  &:hover,
  &:focus,
  &:active {
    opacity: 0.8;
  }

  @include vp-1179 {
    font-size: 12px;
    line-height: 15px;
    padding: 5px 14px 5px 10px;
  }

  @include vp-767 {
    padding: 10px 10px 5px 0;
  }
}

.page-footer__social {
  margin-right: -4px;
  justify-self: flex-end;

  @include vp-1179 {
    margin-right: -7px;
  }

  @include vp-767 {
    margin-right: 0;
    justify-self: flex-start;
  }
}


